<template>
  <div class="container">
    <!-- 获取当前路由的meta.title属性 -->
    <mt-header fixed :title="$route.meta.title">
      <span slot="left" @click="goBack" v-show="showBack">
        <mt-button icon="back">返回</mt-button>
      </span>
    </mt-header>

    <transition name="fade">
      <router-view></router-view>
    </transition>

    <tabbar></tabbar>

  </div>
</template>

<script>
import tabbar from './components/TabbarNm.vue'

export default {
  data () {
    return {
      showBack: false
    }
  },
  created () {
    this.showBack = this.$route.path !== '/home'
    this.checkLogin()
  },

  // 当前路径如果不是home将newVal设置成true,true就会在v-show中显示出来，这样就实现了除Home以外其他路由都有返回
  watch: {
    '$route.path' (newVal) {
      this.showBack = newVal !== '/home'
    }
  },
  methods: {
    goBack () {
      this.$router.go(-1)
    },
    checkLogin () {
      // this.$indicator.open({
      //   text: '加载中'
      // })
      this.$http.get('user').then(res => {
        if (res.data.code === 1) {
          this.$store.commit('user/setUser', res.data.data)
        }
        this.$indicator.close()
      })
    }
  },
  components: {
    tabbar
  }
}
</script>

