import Vue from 'vue'
import Router from 'vue-router'
import Home from './pages/HomeH.vue'
import Category from './pages/CategoryC.vue'
import Shopcart from './pages/ShopcartS.vue'
import User from './pages/UserU.vue'
import Login from './pages/user/LoginL.vue'
import Register from './pages/user/RegisterR.vue'
Vue.use(Router)



const router = new Router({
    mode: 'history',
    routes: [
        { path: '/', redirect: '/home', meta: { title: 'bilibili简化版' } },
        { path: '/home', component: Home, name: 'home', meta: { title: 'bilibili简化版' } },
        { path: '/category', component: Category, name: 'category', meta: { title: '分类' } },
        { path: '/shopcart', component: Shopcart, name: 'shopcart', meta: { title: '购物车' } },
        { path: '/user', component: User, name: 'user', meta: { title: '我的' } },
        { path: '/user/login', component: Login, name: 'login', meta: { title: '登录' } },
        { path: '/user/register', component: Register, name: 'register', meta: { title: '注册' } }
    ],
    linkActiveClass: 'mui-active'
})
// 切换路由时切换页面标题，to是目标路由,from是来源路由,next是下一步操作
router.beforeEach((to, from, next) => {
    if (to.meta.title) {
        document.title = to.meta.title
    }
    next()
})
export default router